import {Component, OnInit} from '@angular/core';
import * as D3 from 'd3';
import {People} from './tree.entity';
import {DataService} from './tree.service';


@Component(
  {
    selector: 'app-tree',
    templateUrl: './tree.component.html',
    styleUrls: ['./tree.component.css'],
  })
export class TreeComponent implements OnInit {
  private peoples: [[People]];
  private options = [];

  private tree;
  private svg: D3.Selection<any>;

  private duration = 750;
  private margin = {top: 20, right: 90, bottom: 30, left: 90};
  private width = 1400 - this.margin.right - this.margin.left;
  private height = 1000 - this.margin.top - this.margin.bottom;
  private colorScale = D3.scaleLinear()
    .domain([0, 1])
    .range(['red', 'green']);
  private widthScale = D3.scaleLinear()
    .domain([1, 80])
    .range([1, 10]);
  private root;
  private g;
  private index = 0;

  constructor(private dataService: DataService) {
  }

  ngOnInit() {
    this.exp2();
  }

  /**
   * 初始化d3对象
   */
  private init() {
    this.svg = D3.select('#app-tree-tree').append('svg')
      .attr('width', this.width + this.margin.right + this.margin.left)
      .attr('height', this.height + this.margin.top + this.margin.bottom);
    this.g = this.svg.append('g')
      .attr('transform', 'translate(' + this.margin.left + ',' + this.margin.top + ')');
  }

  /**
   * 生成node所需要的数据
   * @param data
   * @returns {any}
   */
  private getNodeData(data) {
    const newData = D3.hierarchy(data, (d) => {
      return d.childes;
    });
    if (!newData.children) {
      console.log(newData);
    }
    newData.children.forEach(this.collapse);
    return newData;
  }

  private exp2() {
    this.init();
    this.tree = D3.tree().size([this.height, this.width]);

    this.dataService.getFirstData()
      .then(data => {
        this.root = this.getNodeData(data);
        this.root.x0 = this.height / 2;
        this.root.y0 = 0;
        this.update(this.root);
      });
  }

  /**
   * 设置 子节点
   * @param p 节点数据
   */
  private collapse(p) {
    if (p.children) {
      p._children = p.children;
      for (let i = 0; i < p._children.size; i++) {
        this.collapse(p._children[i]);
      }
      p.children = null;
    }
  }

  /**
   * 生成d3数据
   * @param s
   * @param d
   * @returns {string}
   */
  private diagonal(s, d) {
    const pathStr = `M ${s.y} ${s.x}
            C ${(s.y + d.y) / 2} ${s.x},
              ${(s.y + d.y) / 2} ${d.x},
              ${d.y} ${d.x}`;
    return pathStr;
  }

  /**
   * 更新节点
   * @param source
   */
  private update(source) {
    // Compute the new tree layout.
    const treeData = this.tree(this.root);
    const nodes = treeData.descendants();
    const links = treeData.descendants().slice(1);
    // Normalize for fixed-depth.

    nodes.forEach((d) => {
      d.y = d.depth * 180;
    });
    // Update the nodes…
    const node = this.g.selectAll('g.node')
      .data(nodes, (d) => {
        return d.id || (d.id = ++this.index);
      });

    // Enter any new nodes at the parent's previous position.
    const nodeEnter = node.enter().append('g')
      .attr('class', 'node')
      .attr('transform', (d) => {
        return 'translate(' + source.y0 + ',' + source.x0 + ')';
      })
      .on('click', (d) => {
        return this.click(d);
      });

    nodeEnter.append('circle')
      .attr('class', 'node')
      .attr('r', 1e-6)
      .style('fill', function (d) {
        return d.data.has_child ? 'lightsteelblue' : '#fff';
      })
      .style('stroke', (d) => {
        return this.colorScale(d.data.female / (d.data.female + d.data.male));
      });

    nodeEnter.append('text')
      .attr('dy', '.35em')
      .attr('x', (d) => {
        return d._children || d._children ? -10 : 10;
      })
      .attr('text-anchor', (d) => {
        return d._children || d._children ? 'end' : 'start';
      })
      .text((d) => {
        return d.data.name;
      })
      .style('fill', (d) => {
        return this.colorScale(d.data.female / (20));
      });
    // update
    const nodeUpdate = nodeEnter.merge(node);

    // Transition nodes to their new position.
    nodeUpdate.transition()
      .duration(this.duration)
      .attr('transform', function (d) {
        return 'translate(' + d.y + ',' + d.x + ')';
      });

    nodeUpdate.select('circle.node')
      .attr('r', 10)
      .style('fill', (d) => {
        return d.data.has_child ? 'lightsteelblue' : '#fff';
      })
      .attr('cursor', 'pointer');

    // Transition exiting nodes to the parent's new position.
    const nodeExit = node.exit().transition()
      .duration(this.duration)
      .attr('transform', function (d) {
        return 'translate(' + source.y + ',' + source.x + ')';
      })
      .remove();

    nodeExit.select('circle')
      .attr('r', 1e-6);

    nodeExit.select('text')
      .style('fill-opacity', 1e-6);

    // Update the links…
    const link = this.g.selectAll('path.link')
      .data(links, function (d) {
        return d.id;
      })
      .style('stroke-width', (d) => {
        return this.widthScale(20);
      });

    // Enter any new links at the parent's previous position.
    const linkEnter = link.enter().insert('path', 'g')
      .attr('class', 'link')
      .attr('d', (d) => {
        const o = {x: source.x0, y: source.y0};
        return this.diagonal(o, o);
      })
      .style('stroke-width', (d) => {
        return this.widthScale(10);
      });

    // UPDATE
    const linkUpdate = linkEnter.merge(link);

    // Transition links to their new position.
    linkUpdate.transition()
      .duration(this.duration)
      .attr('d', (d) => {
        return this.diagonal(d, d.parent);
      });

    // Transition exiting nodes to the parent's new position.
    link.exit().transition()
      .duration(this.duration)
      .attr('d', (d) => {
        const o = {x: source.x, y: source.y};
        return this.diagonal(o, o);
      })
      .style('stroke-width', (d) => {
        return this.widthScale(2);
      })
      .remove();

    nodes.forEach((d) => {
      d.x0 = d.x;
      d.y0 = d.y;
    });
  }

  /**
   * 点击节点，有则展开
   * @param d
   * @returns {Promise<void>}
   */
  private click(d) {
    if (d.children) {
      d._children = d.children;
      d.children = null;
    } else {
      if (!Boolean(d._children) && d.data.has_child) {
        console.log(d);
        return this.dataService.getData(d.data.id)
          .then(data => {
            return this.addData(d, data);
          });
      }
      d.children = d._children;
      d._children = null;
    }
    this.update(d);
  }

  /**
   * 添加新的数据到结构树
   * @param node
   * @param data
   */
  private addData(node, data) {
    const nodeData = this.getNodeData(data);
    node.children = nodeData.children;
    this.addDepth(node);
    node._children = null;
    return this.update(node);
  }

  /**
   * 添加深度数据，补丁
   * @param node
   */
  private addDepth(node) {
    if (node.children) {
      node.children.forEach((d2) => {
        d2.depth = node.depth + 1;
        d2.parent = node;
      });
    }
  }

  /**
   * 根据对应的option生成对应的结构树
   * @param {string} text : 选择字符串
   */
  public searchChanged(text: string) {
    console.log(text);
    const i = this.options.indexOf(text);
    console.log(i);
    if (i !== -1) {
      this.extendChild(this.peoples[i]);
    }
    this.options = [];
  }

  /**
   * 从服务器获取数据,并展示给用户选择
   * @param {string} name
   */
  public queryData(name: string) {
    console.log(name);
    this.dataService.queryData(name)
      .then(data => {
        this.peoples = data;
        for (let i = 0; i < data.length; i++) {
          const tmp = [];
          data[i].forEach(people => {
            tmp.push(people.name);
          });
          this.options.push(tmp.join('->'));
        }
        console.log('load ok');
      });
  }

  /**
   * 逆序打开节点，从顶级开始，然后从找到的节点继续下一个查询
   * @param peoples
   */
  private extendChild(peoples) {
    if (peoples) {
      let node = this.root;
      for (let i = peoples.length - 1; i >= 0; i--) {
        // console.log(node, peoples[i].name);
        node = this.checkNode(node, peoples[i]);
      }
    }
  }

  /**
   * 检查父级，直到没有父级，第一层到当前节点，全部打开
   * @param node
   * @param people
   * @returns {any} 正常会返回一个节点，递归的时候有可能不返回
   */
  private checkNode(node, people) {
    if (!node) {
      console.log(node);
    }
    if (node.data.id === people.id) {
      if (node.children) {
        return node;
      } else {
        if (people.childes.length) {
          this.addData(node, people);
        }
        return node;
      }
    } else {
      let newNode = null;
      if (node.children) {
        for (let i = 0; i < node.children.length; i++) {
          newNode = this.checkNode(node.children[i], people);
          if (newNode && newNode !== node) {
            return node.children[i];
          }
        }
      }
    }
  }

}



