import {Injectable} from '@angular/core';
import {People} from './tree.entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';

@Injectable()
export class DataService {
  public people: People;
  public peoples: [[People]];
  private peopleUrl = '/api/people';
  private firstUrl = '/api/people/first';
  private searchUrl = '/api/people/query';
  private headers = new HttpHeaders({
    'Content-Type': 'application/json'
  });
  private cacheStr = 'rootPeople';

  constructor(private http: HttpClient) {
  }

  /**
   * 获取具体的人
   * @param {number} id
   * @returns {Promise<People>}
   */
  getData(id: number = 1) {
    return this.http.get(`${this.peopleUrl}/${id}`, {headers: this.headers})
      .toPromise()
      .then(data => {
        this.people = data['results'] as People;
        return this.people;
      });
  }

  /**
   * 获取第一个人
   * @param {number} id
   * @returns {Promise<People>}
   */
  getFirstData() {
    return this.http.get(this.firstUrl, {headers: this.headers})
      .toPromise()
      .then(data => {
        this.people = data['results'] as People;
        return this.people;
      });
  }

  /**
   * 搜索
   * @param {string} title
   */
  queryData(title: string) {
    return this.http.get(`${this.searchUrl}`, {headers: this.headers, params: {name: title}})
      .toPromise()
      .then(data => {
        this.peoples = data['results'] as [[People]];
        return this.peoples;
      });
  }

  cacheData(people: People) {
    let root = localStorage.getItem(this.cacheStr);
    if (!root) {
      root = JSON.stringify(people);
      localStorage.setItem(this.cacheStr, root);
      return;
    }
    const rootPeople = JSON.parse(root);
    rootPeople.childes.forEach((p, index) => {
      if (p.id === people.id) {
        rootPeople.childes[index] = people;
      }
    });
  }

  getCacheData() {
    const data = localStorage.getItem(this.cacheStr);
    if (data) {
      return JSON.parse(data);
    }
  }
}



